import React from 'react'
import '../style/card.scss'

class Card extends React.Component{

    constructor(props) {
        super(props);
    }
  
    render () {
        let button;
        if (this.props.button) {
            button =  <a href="#" className="outline-button">{this.props.button}</a>;
          } else {
            button = "";
        }
    return (
        <div className="simple-card">
            <h5>{this.props.header}</h5>
            <h3>{this.props.title}</h3>
            <p>{this.props.text}</p>
            <p><strong>{this.props.subtext}</strong></p>
            <br/>
            {button}
        </div>
    );
   }
};
export default Card;