import React from 'react'
import '../style/SplitShowcase.scss'


class SplitShowcase extends React.Component{

    constructor(props) {
        super(props);
        this.state = {
            left : false,
            right: false
        };
    }

    mouseEnterLeft = () =>{
        this.setState({left: true});
    }
    
    mouseExitLeft = () =>{
        this.setState({left: false});
    }
    mouseEnterRight = () =>{
        this.setState({right: true});
    }
    
    mouseExitRight = () =>{
        this.setState({right: false});
    }

    render () {
    return (
        <section className="start-grid">
            <div className={"left-picture split " + (this.state.right ? "thin":"") + (this.state.left ? "wide": "")} onMouseEnter={this.mouseEnterLeft} onMouseLeave={this.mouseExitLeft}>
                <h2 className="teko">I've done Crossfit</h2>
                <a href="#" className="outline-button left-button">GET STARTED</a>
            </div>
            <div className={"right-picture split " + (this.state.right ? "wide":"") + (this.state.left ? "thin": "")} onMouseEnter={this.mouseEnterRight} onMouseLeave={this.mouseExitRight}>
                <h2 className="teko">I am new</h2>
                <a href="#" className="outline-button right-button">GET STARTED</a>
            </div>
      </section>
    );
   }
};
export default SplitShowcase;